// 提交模板
export const sumbitRowModule = {
  "goods_id": '',
  "batch_id": '',
  "batch": '',
  "warehouse_id": '',
  "allocation_id": '',
  "after_warehouse_id": '',
  "after_allocation_id": '',
  "goods_price": '',
  "goods_num": '',
  "goods_cost_price": '',
  "reason": ''
}
export const TableHead = { // 需要传进来一个this对象
  data() {
    return {
      editRules: {
        goods_name: [
          { required: true, message: '请选择商品' }
        ],
        after_warehouse_name: [
          { required: true, message: '请选择仓库' }
        ],
        batch: [
          { required: true, message: '请选择批次' }
        ],
        goods_num: [
          { required: true, message: '请输入不良品数量' }
        ],
        reason: [
          { required: true, message: '请输入原因' }
        ],
      },
      activateScope: {},
      tableColumn: [
        {
          type: "checkbox",
          width: 44
        },
        {
          title: '商品编号',
          field: 'goods_id',
          width: 80
        },
        {
          title: '商品名称',
          field: 'goods_name',
          width: 350,
          editRender: {
            name: 'vGoods',
            getParams: () => {
              return {
                params: {
                  company_id: this.params.company_id
                },
                showObj: {
                  goods_id: "商品编号",
                  goods_name: "商品名称",
                  unit_name: "单位",
                  brand_name: "品牌",
                  specification_type: "规格型号",
                  factory_code: "工厂代码"
                }
              }
            },
            events: {
              change: this.changeGoods
            }
          }
        },
        {
          field: 'batch',
          title: '年周/批次',
          showOverflow: "tooltip",
          showHeaderOverflow: "tooltip",
          width: 200,
          editRender: {
            autoselect: true,
            name: 'vBatch',
            getParams: (scope) => {
              return {
                params: {
                  company_id: this.params.company_id,
                  goods_id: scope.row.goods_id,
                  bill_type: 2,
                   relation_warehouse_id:1,
                  is_incomplete: this.params.order_type == 1 ? 2 : 1
                },
                showObj: {
                  batch: "年周/批次",
                  warehouse_name: "调出仓库",
                  allocation_name: "调出货位",
                  available_stocks: "批次可用库存",
                  stocks: "在库库存",
                  cost_price: "入库单价"
                }
              }
            },
            events: {
              change: (val, { row }) => {
                if (!row.goods_id) {
                  return false
                }
                const {
                  production_time,
                  batch_id,
                  batch,
                  stocks,
                  available_stocks,
                  warehouse_id,
                  unit_name,
                  warehouse_name,
                  allocation_id,
                  allocation_name,
                  cost_price
                } = val;
                Object.assign(row, {
                  production_time,
                  batch_id,
                  batch,
                  stocks,
                  available_stocks,
                  warehouse_id,
                  goods_unit_name: unit_name,
                  warehouse_name,
                  allocation_id,
                  allocation_name,
                  goods_price: cost_price
                });
              }
            }
          }
        },
        {
          title: '单位',
          field: 'goods_unit_name',
          width: 100
        },
        {
          title: '调出仓库',
          field: 'warehouse_name',
          width: 240,
          showOverflow: true
        },
        {
          title: '调出货位',
          field: 'allocation_name',
          width: 130,
          showOverflow: true
        },
        {
          title: '调入仓库',
          field: 'after_warehouse_name',
          width: 150,
          showOverflow: true,
          editRender: {
            name: 'vSelect',
            getParams: () => {
              return {
                url: 'getWarehouseFocus',
                modelKey: 'after_warehouse_id',
                params: {
                  company_id: this.params.company_id,
                  relation_warehouse_id: 1
                },
                itemObj: {
                  id: 'warehouse_id',
                  name: 'warehouse_name'
                }
              }
            },
            events: {
              change: ({
                row
              }, obj) => {
                row.after_warehouse_id = obj.warehouse_id
                row.after_warehouse_name = obj.warehouse_name
                row.after_allocation_id = ''
                row.after_allocation_name = ''
                this.$axMethod.allocationInventoryListFocus({
                  warehouse_id: obj.warehouse_id,
                  is_incomplete: this.params.order_type
                }).then(({
                  data
                }) => {
                  // let allocation = data.data.filter(val => {
                  //   return val.allocation_name == '不良品货位'
                  // })[0];
                  this.$set(row, 'after_allocation_id', data.data[0].allocation_id)
                  this.$set(row, 'after_allocation_name', data.data[0].allocation_name)
                })
              }
            },
          }
        },
        {
          title: '调入货位',
          field: 'after_allocation_name',
          width: 150,
          showOverflow: true
        },
        {
          title: '商品单价',
          field: 'goods_price',
          align: "center", // 列对齐方式
          width: 100
        },
        {
          title: '不良品数量',
          field: 'goods_num',
          headerAlign: "center", // 表头对齐方式
          align: "center", // 列对齐方式
          width: 120,
          editRender: {
            name: 'number',
            events: {
              input: this.handleEdit,
            }
          }
        },
        {
          title: '商品成本',
          field: 'goods_cost_price',
          headerAlign: "center", // 表头对齐方式
          align: "center", // 列对齐方式
          width: 100
        },
        {
          title: '转不良品原因',
          field: 'reason',
          width: 200,
          editRender: {
            name: 'Vinput',
            formatter: 'String'
          }
        },
        // {
        //   title: '备注',
        //   field: 'remark',
        //   width: 200,
        //   editRender: {
        //     name: 'Vinput',
        //     formatter: 'String'
        //   }
        // },
      ]
    }
  },
  methods: {
    // 是否可编辑
    isEdit(scope) {
      if (scope.column.property != "goods_name") {
        if (!Boolean(scope.row.goods_id)) {
          this.$message.error({
            message: "请选择商品信息"
          });
          return false
        }
      } else if (!(this.params.company_id)) {
        this.$message.error({
          message: "请选择组织"
        });
        return false
      }
      return true
    },
  }
}
export const saleRowModule = {}
