<template>
  <div>
    <!-- 添加修改库存入库单 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">不良品管理</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}新增不良品</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" ref="ruleForm" class="demo-form-inline">
        <el-form-item label="库存组织" size="mini" prop="company_id" :rules="rules.company_id">
          <erpCompanyList v-model="params.company_id" :fatherParams="params" @change="companyChange" :disabled="disabled"></erpCompanyList>
        </el-form-item>
        <el-form-item label="部门" size="mini" filterable>
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable>
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          {{(function(){if(params.business_id == 0)params.business_id = null})()}}
          <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok(1)">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="单据类型" size="mini" :disabled="disabled" prop="order_type" :rules="{required: true,message:'请选择单据类型'}">
          {{(function(){if(params.order_type == 0)params.order_type = null})()}}
          <el-select v-model="params.order_type" placeholder="请选择" @change="clearTable">
            <el-option label="良品转不良品" :value="1"></el-option>
            <el-option label="不良品转良品" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.conversion_sn" disabled></el-input>
        </el-form-item>
        <el-form-item label="备注" size="mini">
          <el-input v-model="params.abstract" type="text" maxlength="300"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deleteF" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <xTable ref='xTable' :isEdit='isEdit' :edit-rules='editRules' :tableColumn='tableColumn' :showFooter='true' :totalArray="['goods_cost_price','goods_num','goods_price']" />
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit('ruleForm')">确 定</el-button>
    </div>
  </div>
</template>
<script>
import {
  CompanySwitchClear,
  opthionList
} from "common/utils/public_function/commonUtils";
import { TableHead, saleRowModule } from "./config/IncompleteNew";
export default {
  name: "IncompleteNew",
  mixins: [TableHead],
  data() {
    return {
      title: "新增",
      loading: false,
      disabled: false, //是否禁用按钮
      params: {
        order_type: 1
      }
    };
  },
  methods: {
    // 仓库改变
    warehouseChange(id) {
      this.$refs.xTable._proxy("getTableData").fullData.forEach(val => {
        val.allocation_name = "";
        val.allocation_id = "";
      });
      var box = this.warehouseList.filter(val => val.warehouse_id === id);
      this.params.warehouse_name = box[0] ? box[0].warehouse_name : "";
    },
    // 商品改变
    changeGoods(val, scope) {
      var { row } = scope;
      row.goods_id = val.goods_id;
      row.unit_name = val.unit_name;
      // 进行计算
      var box = this.$refs.xTable._proxy("getTableData").fullData.concat([]);
      if (box[box.length - 1].goods_id) {
        this.addF();
      }
    },
    addF() {
      this.$refs.xTable._proxy(
        "insertAt",
        this.$publicFution.deepCopy(saleRowModule),
        -1
      );
    },
    clearTable() {
      if (this.params.order_type === 1) {
        this.editRules.reason = [{ required: true, message: "请输入原因" }];
      } else {
        this.editRules.reason = [{ required: false, message: "请输入原因" }];
      }
      this.tableColumn.forEach(val => {
        if (val.field === "reason") {
          if (this.params.order_type === 1) {
            val.title = "转不良品原因";
          } else {
            val.title = "转良品原因";
          }
        }
      });
      this.$refs.xTable._proxy("loadData", []);
    },
    companyChange() {
      CompanySwitchClear(this, this.params);
      this.$refs.xTable._proxy("loadData", []);
    },
    // 部门改变
    departmentChange() {
      this.$set(this.params, "business_id", "");
    },
    goBack() {
      this.$router.push({
        path: "/inventory/Incomplete"
      });
    },
    // 删行
    deleteF() {
      if (this.$refs.xTable._proxy("getCheckboxRecords").length) {
        this.$refs.xTable._proxy("removeSelecteds");
      } else {
        this.$message({
          message: "请先选择要删除的订单！",
          type: "warning"
        });
      }
    },
    Commit(val) {
      let submitObj = {
        ...this.params,
        goods_json: JSON.stringify(val)
      };
      let datas;
      if (this.params.conversion_id) {
        datas = this.$axMethod.ConversionsEditOrder(submitObj);
      } else {
        datas = this.$axMethod.ConversionsAddOrder(submitObj);
      }
      datas.then(({ data }) => {
        if (data.code == 200) {
          this.$message({
            message: data.msg,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let tableBox = this.$refs.xTable
            ._proxy("getTableData")
            .fullData.concat([]);
          if (!tableBox[tableBox.length - 1].goods_id) {
            tableBox.pop();
          }
          if (!tableBox.length) {
            this.$message.error("请完善表格数据");
            return false;
          }
          this.$refs.xTable._proxy("validate", tableBox).then(errMap => {
            this.Commit(tableBox);
          });
        }
      });
    },
    // 计算规则
    handleEdit({ row, column }) {
      var obj = {
        num: "goods_num", // 商品数量的key
        price: "goods_price", // 商品价格的key
        totalPrice: "goods_cost_price" // 商品小计金额
      };
      this.$publicFution.computeTotal(row, obj, column.property, this);
    }
  },
  mounted() {
    this.conversion_id = this.$route.query.conversion_id;
    this.title = this.conversion_id ? "修改" : "新增";
    if (this.conversion_id) {
      this.$axios({
        method: "get",
        url: this.$api.DefectiveProductGetOrderInfo,
        params: {
          conversion_id: this.conversion_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          let Particulars = res.data.data;
          let { goods_list, ...datas } = Particulars;
          this.params = datas;
          if (this.params.order_type === "添加残次品") {
            this.params.order_type = 1;
          } else {
            this.params.order_type = 2;
          }
          this.$refs.xTable._proxy("loadData", goods_list);
          this.addF();
          this.disabled = true;
          this.employeeList = [
            {
              employee_id: datas.business_id,
              employee_name: datas.business_name
            }
          ];
          opthionList(this, datas);
        }
      });
    } else {
      this.$set(this, "params", {
        business_time: this.$publicFution.formatData(new Date()),
        company_id: "",
        company_name: "",
        // business_id: Number(localStorage.employee_id),
        order_type: 1
      });
      opthionList(this, window.localStorage);
      setTimeout(() => {
        this.addF();
      }, 200);
    }
  }
};
</script>
<style lang="less" scoped>
.styleInput > .el-input {
  width: auto !important;
}

.input-with-select .el-input-group__prepend {
  background-color: #fff;
}

